import { defineComponent } from "vue";
import { useStore } from "vuex";
let store;
let value;
export default defineComponent({
    name: "SelectPopUp",
    data() {
        return {
            content: '',
        };
    },
    created() {
        store = useStore();
        value = store.getters["SelectPopUp/getPopupValue"];
        if (value === "logout") {
            this.content = "로그아웃 하시겠습니까?";
        }
        else if (value === "restore") {
            this.content = "해당 알림을 복구하시겠습니까?";
        }
        else if (value === "delete") {
            this.content = "해당 알림을 삭제하시겠습니까? <br> (삭제 시 복구 불가)";
        }
    },
    methods: {
        cancelBtn() {
            store.dispatch("SelectPopUp/selectPopUp", { showModal: false, logoutValue: '0', restoreValue: '0', deleteValue: '0' });
        },
        confirmBtn() {
            if (value === "logout") {
                store.dispatch("SelectPopUp/selectPopUp", { showModal: false, logoutValue: '1' });
            }
            else if (value === "restore") {
                store.dispatch("SelectPopUp/selectPopUp", { showModal: false, logoutValue: '0', restoreValue: '1' });
            }
            else if (value === "delete") {
                store.dispatch("SelectPopUp/selectPopUp", { showModal: false, logoutValue: '0', deleteValue: '1' });
            }
        }
    }
});
