import { defineComponent } from "vue";
import router from "@/router";
import { useStore } from "vuex";
import SelectPopUp from "@/views/PopUp/SelectPopUp.vue";
import ConfirmPopUp from "@/views/PopUp/ConfirmPopUp.vue";
import { WNInterface } from "vue-wni";
import Loader from "@/views/Loder/Loader.vue";
let store;
export default defineComponent({
    name: "HiddenList",
    data() {
        return {
            pushList: [],
            selectIndex: 0,
            userId: '',
            noDataText: '',
            removeValue: 0,
            hiddenListValue: 0,
            loginToggle: false,
        };
    },
    async created() {
        store = useStore();
        this.userId = await this.$storage.getItem("userId");
        await this.getHiddenPushList();
    },
    computed: {
        //복구 팝업 / 삭제 팝업
        selectPopupShowModal() {
            if (store.getters["SelectPopUp/getRestoreValue"] === '1') {
                this.changeRestore();
            }
            else if (store.getters["SelectPopUp/getDeleteValue"] === '1') {
                this.changeDelete();
            }
            return store.getters["SelectPopUp/getSelectModal"];
        },
        //최신버전 팝업
        confirmPopUpShowModal() {
            return store.getters["ConfirmPopUp/getConfirmModal"];
        }
    },
    methods: {
        async getHiddenPushList() {
            if (!this.loginToggle) {
                this.loginToggle = true;
                //메인 리스트 가져오기
                const VALUE = await store.dispatch("PushList/pushList", JSON.stringify({ userId: this.userId }));
                this.loginToggle = false;
                if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                    this.pushList = VALUE.datas[0]['appUserPushMessageVO'];
                    if (this.pushList.length == 0) {
                        this.noDataText = "학습 알림이 존재하지 않습니다.";
                    }
                    else {
                        this.hiddenListValue = this.setHiddenLength(this.pushList);
                        if (this.hiddenListValue === 0) {
                            this.noDataText = "학습 알림이 존재하지 않습니다.";
                        }
                    }
                }
                else if (VALUE === 'tokenExpire') {
                    const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: this.userId }));
                    if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                        await this.getHiddenPushList();
                    }
                    else {
                        await this.$router.replace({ name: 'Login' });
                    }
                }
                else {
                    this.serverError();
                }
            }
        },
        deleteBtn(index) {
            store.dispatch("SelectPopUp/selectPopUp", { showModal: true, value: "delete" });
            this.selectIndex = index;
            this.wnDialogShowSelect();
        },
        restoreBtn(index) {
            store.dispatch("SelectPopUp/selectPopUp", { showModal: true, value: "restore" });
            this.selectIndex = index;
            this.wnDialogShowSelect();
        },
        async changeDelete() {
            let pushCd = this.pushList[this.selectIndex]["pushCd"];
            //삭제 보내기
            const VALUE = await store.dispatch("PushList/pushDelete", JSON.stringify({ pushCd: pushCd, delYn: 'Y' }));
            if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                this.pushList[this.selectIndex]['delYn'] = 'Y';
                this.removeValue = this.removeValue + 1;
                if (this.removeValue == this.hiddenListValue) {
                    this.noDataText = "학습 알림이 존재하지 않습니다.";
                }
            }
            else if (VALUE === 'tokenExpire') {
                const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: this.userId }));
                if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                    await this.changeDelete();
                }
                else {
                    await this.$router.replace({ name: 'Login' });
                }
            }
            else {
                //실패 한 경우
                this.popUpDialog("삭제에 실패하였습니다.<br> 잠시 후 다시 시도해주세요.");
            }
        },
        async changeRestore() {
            let pushCd = this.pushList[this.selectIndex]["pushCd"];
            //숨김 해제 (복구)
            const VALUE = await store.dispatch("PushList/pushRestore", JSON.stringify({ pushCd: pushCd, viewYn: 'Y' }));
            if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                this.pushList[this.selectIndex]['viewYn'] = 'Y';
                this.removeValue = this.removeValue + 1;
                if (this.removeValue == this.hiddenListValue) {
                    this.noDataText = "학습 알림이 존재하지 않습니다.";
                }
            }
            else if (VALUE === 'tokenExpire') {
                const VALUE = await store.dispatch("ChangeToken/changeToken", JSON.stringify({ userId: this.userId }));
                if (VALUE !== "error" && VALUE !== "tokenExpire" && VALUE !== "notToken") {
                    await this.changeRestore();
                }
                else {
                    await this.$router.replace({ name: 'Login' });
                }
            }
            else {
                //실패 한 경우
                this.popUpDialog("삭제에 실패하였습니다.<br> 잠시 후 다시 시도해주세요.");
            }
        },
        //서버 에러
        serverError() {
            this.popUpDialog('서버와 연결이 불안정합니다. <br> 잠시 후 다시도해주세요.');
        },
        backBtn() {
            router.back();
        },
        //보여지는 날짜 설정해주기
        setDate(item, index) {
            //번호가 0번쨰가 아닐 때
            if (index !== 0) {
                let beforeValue = index - 1;
                if (beforeValue !== 0) {
                    if (this.changeDate(this.pushList[beforeValue]['sendDttm']) === item) {
                        while (this.pushList[beforeValue]['viewYn'] !== 'N' && beforeValue > 0) {
                            beforeValue = beforeValue - 1;
                        }
                        if (this.changeDate(this.pushList[beforeValue]['sendDttm']) === item && this.pushList[beforeValue]['viewYn'] === 'N') {
                            return '';
                        }
                    }
                    else {
                        if (this.pushList[index]['viewYn'] === 'N') {
                            return item;
                        }
                        else {
                            return '';
                        }
                    }
                }
                else {
                    if (this.changeDate(this.pushList[beforeValue]['sendDttm']) === item) {
                        return '';
                    }
                    else {
                        if (this.pushList[index]['viewYn'] === 'N') {
                            return item;
                        }
                        else {
                            return '';
                        }
                    }
                }
            }
            //번호가 0 번째 일때
            else {
                if (this.pushList[index]['viewYn'] === 'N') {
                    return item;
                }
                else {
                    return '';
                }
            }
        },
        setHiddenLength(pushList) {
            let count = 0;
            pushList.forEach((v) => {
                if (v['viewYn'] === 'N') {
                    count = count + 1;
                }
            });
            return count;
        },
        changeDate(dateSet) {
            let year = dateSet.substring(0, 4);
            let month = dateSet.substring(4, 6);
            let date = dateSet.substring(6, 8);
            return year + "-" + month + "-" + date;
        },
        changeDate2(dateSet) {
            let hour = this.convertTime(Number(dateSet.substring(8, 10)));
            let minute = dateSet.substring(10, 12);
            // let second = dateSet.substring(12,14);
            // return hour+"시 "+minute+"분 "+second+"초"
            return hour + "시 " + minute + "분";
        },
        //시간 변경 해주기
        convertTime(number) {
            if (number >= 12) {
                let setNumber = 0;
                if (number != 12) {
                    setNumber = number - 12;
                }
                else {
                    setNumber = number;
                }
                if (setNumber < 10) {
                    return String("오후 " + "0" + setNumber);
                }
                else {
                    return String("오후 " + setNumber);
                }
            }
            else {
                if (number < 10) {
                    return String("오전 " + "0" + number);
                }
                else {
                    return String("오전 " + number);
                }
            }
        },
        popUpDialog(content) {
            store.dispatch("ConfirmPopUp/confirmPopUp", { showModal: true, content: content });
            this.wnDialogShow();
        },
        async wnDialogShow() {
            await WNInterface.execute("wnDialogShow", {
                callback: WNInterface.cb(async (data) => {
                    if (data.status == "CLOSE") {
                        store.dispatch("ConfirmPopUp/confirmPopUp", { showModal: false });
                    }
                })
            });
        },
        async wnDialogShowSelect() {
            await WNInterface.execute("wnDialogShow", {
                callback: WNInterface.cb(async (data) => {
                    if (data.status == "CLOSE") {
                        store.dispatch("SelectPopUp/selectPopUp", { showModal: false });
                    }
                })
            });
        },
    },
    components: {
        SelectPopUp,
        ConfirmPopUp,
        Loader
    },
});
